$prefix: '.wc-booster';

#{$prefix}-wishlist-button-wrapper {
	display: flex;
	gap: 10px;
    z-index: 99;
 
	#{$prefix}-wishlist-button{
		padding: 0;
		background: transparent;
		border: none;
		cursor: pointer;
		position: relative;
		i.fa{
			color: #cbc2c2;
		}

		
		.wishlist-label{
			background: #c5c5c5;
			padding: 10px;
			border-radius: 3px;
			position: absolute;
			width: 124px;
			right: 0;
			margin: auto;
			opacity: 0;
			visibility: hidden;
			transition: all .3s;
		}

		&.added {
			i{
				color: #e53737;
				font-weight: 900;

				&:before {
					content: "\f004";
				}
			}
		}

		&.loading {
			padding-right: 0 !important;
			&:after {
				
				border-top: 2px solid #000000;
				width: 8px;
				height: 8px;				
				right: 10px;
				top: 8px;
			}
		}
	}

		&:hover{
		.wishlist-label{
			opacity: 1;
			visibility: visible;
		}
	}
}


.products-block-post-template {
	li.product{
		position: relative;
	}
}
.wp-block-post-template{
	li.type-product{
		position: relative;
	}
}

.single-product{
	.wp-block-column{
		position: relative;
	}
}